.wrapper{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 19px 16px 16px 0;
  border-bottom: 1px solid rgba(135, 135, 135, 0.2);
  border-radius: 8px;;
}

.right{
  display: flex;
  align-items: center;
  gap: 24px;
}

.img{
  object-fit: contain;
  width: 100%;
  max-width: 64px;
  height: auto;
}

.info{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;

  &__name{
    font-weight: 300;
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.408px;
    color: #424242;
  }

  &__info{
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__text{
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.408px;
    color: #878787;
    span{
      margin-left: 3px;
    }
    &:first-child{
      display: flex;
      align-items: center;
      gap: 10px;
      &::after{
        content: "-";
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: #878787;
      }
    }
  }
}

.fav{
  display: flex;
  justify-content: flex-end;
  &__btn{
    border: none;
    background: rgba(255,255,255,0);
    cursor: pointer;
    padding: 0;
  }
}

.price{
  display: flex;
  align-items: flex-end;
  gap: 8px;

  &__text{
    font-weight: 300;
    font-size: 11px;
    line-height: 22px;
    letter-spacing: -0.408px;
    color: #878787;
  }

  &__current{
    font-weight: 400;
    font-size: 17px;
    line-height: 22px;
    text-align: right;
    letter-spacing: -0.408px;
    color: #424242;
  }
}


