.wrapper{
  background: #E5E5E5;
  width: 100vw;
  height: 100%;
}

.content{
  max-width: 1048px;
  margin: 0 auto;
  display: flex;
  gap: 24px;

  &__left{
    flex: 0 0 360px;
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  &__right{
    flex: 0 0 664px;
  }
}